//
// Buttons
// --------------------------------------------------
.btn-default,
.btn-primary,
.btn-success,
.btn-info,
.btn-warning,
.btn-danger {
  .box-shadow(none);
  &:active,
  &.active {
    .box-shadow(none);
  }
  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    .box-shadow(none);
  }
  .badge {
    text-shadow: none;
  }
}

.btn {
  min-width: @btn-width;
  font-size: @btn-font-size;
  border:none;
  border-radius: @btn-radius;
  &.btn-smin{
    min-width: @btn-sm-width;
    font-size: @btn-sm-font-size;
  }
  &.btn-default{
    background: @btn-default-bg-color;
  }
  &.btn-primary{
    background: @btn-primary-bg-color;
    &:active:focus{
      background: darken(@btn-primary-bg-color, 6.5%)
    }
  }
  &:active,
  &:focus,
  &:hover,
  &:active:focus{
    outline: none;
    outline-offset: 0;
  }
}
.dropup,
.dropdown{
  
  .btn-default{
		min-width: 180px;
		min-height: 32px;
		padding: 9px 10px;
		color: #333;
		font-size: 12px;
		box-sizing: border-box;
		text-align: center;
		.fa{
			float: right;
			margin-top: 3px;
		}
	}
	.btn-select {padding:1px 10px;}
}

